$fcLight: #e3e3e5;

.app-close {
  height: 18px;
  width: 18px;
  display: block;
  background: url(../images/close.png) no-repeat center center;
  background-size: 100% auto;
  cursor: pointer;

  &.medium {
    width: 16px;
    height: 16px;
  }

  &.small {
    width: 10px;
    height: 10px;
  }
}

.app-zoom {
  height: 18px;
  width: 18px;
  position: absolute;
  right: 15px;
  top: 15px;
  background: url(../images/zoom.png) no-repeat;
  background-size: 100% auto;
  z-index: 9;
  cursor: pointer;

  &.in {
    background-position: 0 100%;
  }
}

.fc-up {
  color: #91ca8b;

  &:before {
    content: '↑';
  }
}

.fc-down {
  color: #dd6a66;

  &:before {
    content: '↓';
  }
}

.mt20 {
  margin-top: 20px;
}

.mt30 {
  margin-top: 30px;
}